.container {
    min-height: 100vh;
    background: linear-gradient(180deg, #0a0a0f 0%, #12121a 100%);
}

.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 32px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.08);
    background: rgba(10, 10, 15, 0.8);
    backdrop-filter: blur(20px);
    position: sticky;
    top: 0;
    z-index: 100;
}

.navBrand {
    font-size: 24px;
    font-weight: 700;
    background: linear-gradient(135deg, #faa61a 0%, #f04747 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.navRight {
    display: flex;
    align-items: center;
    gap: 20px;
}

.dashLink {
    font-size: 14px;
    color: #a0a0b0;
    transition: color 0.2s;
}

.dashLink:hover {
    color: white;
}

.logoutBtn {
    padding: 8px 16px;
    background: rgba(240, 71, 71, 0.1);
    color: #f04747;
    border: 1px solid rgba(240, 71, 71, 0.3);
    border-radius: 8px;
    font-size: 14px;
    cursor: pointer;
    transition: all 0.2s;
}

.logoutBtn:hover {
    background: rgba(240, 71, 71, 0.2);
}

.main {
    max-width: 1400px;
    margin: 0 auto;
    padding: 40px 24px;
}

.header {
    margin-bottom: 32px;
}

.header h1 {
    font-size: 32px;
    font-weight: 700;
    margin-bottom: 8px;
}

.header p {
    color: #6b6b7b;
}

.tabs {
    display: flex;
    gap: 8px;
    margin-bottom: 24px;
    padding: 4px;
    background: rgba(26, 26, 36, 0.6);
    border-radius: 12px;
    width: fit-content;
    position: relative;
}

.tab {
    padding: 12px 24px;
    background: transparent;
    color: #a0a0b0;
    border: none;
    border-radius: 8px;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.34, 1.56, 0.64, 1);
    position: relative;
    z-index: 1;
}

.tab:hover {
    color: white;
}

.tab.active {
    background: #5865f2;
    color: white;
    box-shadow: 0 4px 12px rgba(88, 101, 242, 0.3);
}

.card {
    background: rgba(26, 26, 36, 0.6);
    backdrop-filter: blur(20px);
    border: 1px solid rgba(255, 255, 255, 0.08);
    border-radius: 16px;
    padding: 24px;
}

.cardHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    padding-bottom: 16px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.06);
}

.cardHeader h2 {
    font-size: 18px;
    font-weight: 600;
}

.count {
    font-size: 13px;
    color: #6b6b7b;
}

.tableWrapper {
    overflow-x: auto;
}

.table {
    width: 100%;
    border-collapse: collapse;
}

.table th,
.table td {
    padding: 14px 16px;
    text-align: left;
    border-bottom: 1px solid rgba(255, 255, 255, 0.04);
}

.table th {
    font-size: 12px;
    font-weight: 600;
    color: #6b6b7b;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.table td {
    font-size: 14px;
}

.table tr {
    transition: all 0.2s ease;
}

.table tr:hover td {
    background: rgba(255, 255, 255, 0.03);
}

.table tr:hover {
    transform: translateX(2px);
}

.userCell {
    display: flex;
    align-items: center;
    gap: 10px;
}

.avatar {
    width: 28px;
    height: 28px;
    border-radius: 50%;
}

.adminBadge {
    padding: 2px 8px;
    background: rgba(250, 166, 26, 0.15);
    color: #faa61a;
    font-size: 11px;
    font-weight: 600;
    border-radius: 10px;
}

.mono {
    font-family: 'Monaco', 'Consolas', monospace;
    font-size: 12px;
    color: #a0a0b0;
}

.truncate {
    max-width: 300px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 12px;
    color: #6b6b7b;
}

.badgeSuccess {
    display: inline-flex;
    padding: 4px 10px;
    background: rgba(67, 181, 129, 0.15);
    color: #43b581;
    font-size: 12px;
    font-weight: 600;
    border-radius: 20px;
}

.badgeDanger {
    display: inline-flex;
    padding: 4px 10px;
    background: rgba(240, 71, 71, 0.15);
    color: #f04747;
    font-size: 12px;
    font-weight: 600;
    border-radius: 20px;
}

.actions {
    display: flex;
    gap: 8px;
}

.btnSmSuccess {
    padding: 6px 12px;
    background: rgba(67, 181, 129, 0.15);
    color: #43b581;
    border: 1px solid rgba(67, 181, 129, 0.3);
    border-radius: 6px;
    font-size: 12px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s;
}

.btnSmSuccess:hover {
    background: rgba(67, 181, 129, 0.25);
}

.btnSmDanger {
    padding: 6px 12px;
    background: rgba(240, 71, 71, 0.15);
    color: #f04747;
    border: 1px solid rgba(240, 71, 71, 0.3);
    border-radius: 6px;
    font-size: 12px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s;
}

.btnSmDanger:hover {
    background: rgba(240, 71, 71, 0.25);
}

.btnPrimary {
    padding: 10px 20px;
    background: #5865f2;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s;
}

.btnPrimary:hover {
    background: #4752c4;
}

.emptyState {
    text-align: center;
    padding: 40px;
    color: #6b6b7b;
}

.loading {
    display: flex;
    justify-content: center;
    padding: 60px;
}

.spinner {
    width: 40px;
    height: 40px;
    border: 3px solid rgba(88, 101, 242, 0.2);
    border-top-color: #5865f2;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

/* Modal with Enhanced Animation */
.modalOverlay {
    position: fixed;
    inset: 0;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    backdrop-filter: blur(8px);
    animation: overlayIn 0.3s ease;
}

@keyframes overlayIn {
    from {
        opacity: 0;
        backdrop-filter: blur(0);
    }

    to {
        opacity: 1;
        backdrop-filter: blur(8px);
    }
}

.modal {
    background: #1a1a24;
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 16px;
    padding: 28px;
    width: 100%;
    max-width: 420px;
    animation: modalBounceIn 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
}

@keyframes modalBounceIn {
    0% {
        opacity: 0;
        transform: scale(0.85) translateY(-20px);
    }

    60% {
        transform: scale(1.02) translateY(0);
    }

    100% {
        opacity: 1;
        transform: scale(1) translateY(0);
    }
}

.modal h3 {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 8px;
}

.modal p {
    font-size: 14px;
    color: #a0a0b0;
    margin-bottom: 24px;
}

.formGroup {
    margin-bottom: 16px;
}

.formGroup label {
    display: block;
    font-size: 13px;
    font-weight: 500;
    color: #a0a0b0;
    margin-bottom: 8px;
}

.input,
.select {
    width: 100%;
    padding: 12px 16px;
    background: rgba(0, 0, 0, 0.3);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 10px;
    color: white;
    font-size: 14px;
    transition: all 0.2s;
}

.input:focus,
.select:focus {
    outline: none;
    border-color: #5865f2;
}

.select {
    cursor: pointer;
}

.modalActions {
    display: flex;
    gap: 12px;
    margin-top: 24px;
}

.btnSecondary {
    flex: 1;
    padding: 12px;
    background: rgba(255, 255, 255, 0.05);
    color: #a0a0b0;
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 10px;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s;
}

.btnSecondary:hover {
    background: rgba(255, 255, 255, 0.1);
}

.btnSuccess {
    flex: 1;
    padding: 12px;
    background: #43b581;
    color: white;
    border: none;
    border-radius: 10px;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s;
}

.btnSuccess:hover {
    background: #3aa373;
}

.btnDanger {
    flex: 1;
    padding: 12px;
    background: #f04747;
    color: white;
    border: none;
    border-radius: 10px;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s;
}

.btnDanger:hover {
    background: #d63b3b;
}

/* Accounts Grid */
.accountsGrid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
    gap: 20px;
}

.userAccountCard {
    background: rgba(255, 255, 255, 0.03);
    border: 1px solid rgba(255, 255, 255, 0.08);
    border-radius: 12px;
    padding: 20px;
}

.userAccountHeader {
    padding-bottom: 16px;
    margin-bottom: 16px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.06);
}

.userAccountName {
    font-size: 15px;
    font-weight: 600;
    color: white;
}

.userAccountMeta {
    display: block;
    font-size: 12px;
    color: #6b6b7b;
    margin-top: 2px;
}

.accountsList {
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.accountItem {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 12px;
    background: rgba(255, 255, 255, 0.02);
    border-radius: 10px;
}

.robloxAvatar {
    width: 40px;
    height: 40px;
    border-radius: 8px;
    background: rgba(255, 255, 255, 0.1);
}

.accountDetails {
    flex: 1;
}

.accountLink {
    font-weight: 600;
    color: white;
    text-decoration: none;
    transition: color 0.2s;
}

.accountLink:hover {
    color: #5865f2;
}

.accountMeta {
    display: block;
    font-size: 11px;
    color: #6b6b7b;
    margin-top: 2px;
}

.noAccounts {
    text-align: center;
    color: #6b6b7b;
    font-size: 13px;
    padding: 16px;
}

.viewButton {
    padding: 6px 12px;
    background: rgba(88, 101, 242, 0.15);
    color: #5865f2;
    font-size: 12px;
    font-weight: 500;
    border-radius: 6px;
    transition: all 0.2s;
}

.accountItem:hover .viewButton {
    background: #5865f2;
    color: white;
}

/* Account Modal */
.accountModalHeader {
    display: flex;
    align-items: center;
    gap: 16px;
    margin-bottom: 24px;
}

.accountModalAvatar {
    width: 64px;
    height: 64px;
    border-radius: 12px;
    background: rgba(255, 255, 255, 0.1);
}

.accountModalHeader h3 {
    margin: 0 0 4px;
}

.profileLink {
    font-size: 13px;
    color: #5865f2;
    text-decoration: none;
}

.profileLink:hover {
    text-decoration: underline;
}

.accountDetailsList {
    display: flex;
    flex-direction: column;
    gap: 12px;
    margin-bottom: 20px;
}

.detailRow {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.06);
}

.detailLabel {
    font-size: 13px;
    color: #6b6b7b;
}

.detailValue {
    font-size: 14px;
    color: white;
    font-family: 'Monaco', 'Consolas', monospace;
}

.cookieSection {
    margin: 20px 0;
    padding: 16px;
    background: rgba(0, 0, 0, 0.3);
    border-radius: 10px;
}

.cookieBox {
    margin: 10px 0;
    padding: 12px;
    background: rgba(0, 0, 0, 0.4);
    border-radius: 8px;
    overflow: hidden;
}

.cookieBox code {
    font-family: 'Monaco', 'Consolas', monospace;
    font-size: 11px;
    color: #a0a0b0;
    word-break: break-all;
}

.btnCopy {
    width: 100%;
    padding: 10px;
    background: rgba(88, 101, 242, 0.15);
    color: #5865f2;
    border: 1px solid rgba(88, 101, 242, 0.3);
    border-radius: 8px;
    font-size: 13px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s;
}

.btnCopy:hover {
    background: #5865f2;
    color: white;
}